"use client";

import Menu from "@/components/Menu";
import Image from "next/image";
import Link from "next/link";
import { useEffect, useState } from "react";

// User info component to display name and branch
const UserInfo = () => {
  const [userName, setUserName] = useState<string>("");
  const [userRole, setUserRole] = useState<string>("");
  const [branchName, setBranchName] = useState<string>("");

  // Branch mapping function
  const getBranchNameById = (id: string | null): string => {
    if (!id) return "";

    const branchMap: {[key: string]: string} = {
      "1": "Mahiyanganaya Branch",
      "2": "Mahaoya Branch"
    };

    return branchMap[id] || `Branch ${id}`;
  };

  useEffect(() => {
    // Get user info from localStorage
    const storedName = localStorage.getItem("userName");
    const storedRole = localStorage.getItem("role");
    const storedBranch = localStorage.getItem("branchName");
    const storedBranchId = localStorage.getItem("branchId");

    if (storedName) setUserName(storedName);
    if (storedRole) setUserRole(storedRole);

    // Use stored branch name if available, otherwise use the branch ID mapping
    if (storedBranch) {
      setBranchName(storedBranch);
    } else if (storedBranchId) {
      setBranchName(getBranchNameById(storedBranchId));
    }
  }, []);

  if (!userName && !userRole) return null;

  // Debug localStorage values - only run on client side
  useEffect(() => {
    if (typeof window !== 'undefined') {
      console.log("UserInfo - localStorage values:");
      console.log("userName:", localStorage.getItem("userName"));
      console.log("role:", localStorage.getItem("role"));
      console.log("branchName:", localStorage.getItem("branchName"));
      console.log("branchId:", localStorage.getItem("branchId"));
    }
  }, []);

  return (
    <div className="mt-4 p-3 bg-white rounded-lg shadow-sm">
      <div className="flex items-center space-x-3">
        <div className="w-10 h-10 rounded-full bg-[#F0A500] flex items-center justify-center">
          <span className="text-white font-bold">
            {userName ? userName.charAt(0) : "U"}
          </span>
        </div>
        <div>
          <p className="font-medium text-sm">{userName || "User"}</p>
          <p className="text-xs text-gray-500">{userRole || "Guest"}</p>

          {/* Always show branch section for Store Managers, with fallback text if no branch */}
          {userRole === "Store Manager" && (
            <div className="mt-1 p-1 bg-blue-50 rounded border border-blue-200">
              <p className="text-xs font-medium text-blue-700">
                {branchName ? `Managing: ${branchName}` : "No branch assigned"}
              </p>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

// Main layout component
export default function DashboardLayout({
  children,
}: Readonly<{
  children: React.ReactNode;
}>) {
  return (
    <div className="h-screen flex bg-white">
      {/* LEFT - Scrollable Menu */}
      <div className="w-[14%] md:w-[8%] lg:w-[16%] xl:w-[20%] p-4 bg-[#FFE569] h-screen overflow-y-auto">
        <Link
          href="/"
          className="flex items-center justify-center lg:justify-start gap-2"
        >
          <Image src="/logo.png" alt="logo" width={32} height={80} />
          <span className="hidden lg:block font-bold text-black">
            S Lanka Jewellery
          </span>
        </Link>

        {/* User info component */}
        <UserInfo />

        <Menu />
      </div>

      {/* RIGHT - Main Content */}
      <div className="w-[86%] md:w-[92%] lg:w-[84%] xl:w-[80%] bg-white overflow-y-auto flex flex-col p-6">
        {children}
      </div>
    </div>
  );
}
